body {
  font-family:"Final Fantasy Script Collection - Final Fantasy VII";
  font-size: 40px;
  background-color: black;
  color: white;
  font-smooth: never;
  -webkit-font-smoothing : none;
  line-height: 20px;
}
*:focus {
  outline: none;
