.progress {
  background-color: rgba(0, 0, 0, 0.5);
  padding: 3px;
  position: relative;
  margin-bottom: 1rem;
  height: 12px;
}
.progress > .box {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.4);
  top: 50%;
  left: 4%;
  width: 45px; height: 12px;
}
.progress > .subtitle {
  position: absolute;
  top: 50%;
  left: 6%;
  font-family:"Pixel Cyr Normal";
  background-color: rgba(0, 0, 0, 0.4);
  height: 20px;
  font-size: 20px;
  color: white;
  text-transform: uppercase;
  display: inline-block;
  line-height: 20px;
  -webkit-transform: scale(1.6, 0.8);
}
.progress > .bar {
  background-image: linear-gradient(to right, #2C6776, #4EA35B);
  height: 12px;
}
.progress.primary > .bar {
  background-image: linear-gradient(to right, #3E8CA5, #49a7c6);
  height: 12px;
}
.progress.success > .bar {
  background-image: linear-gradient(to right, #4EA35B, #5dc66d);
  height: 12px;
}
.progress.warning > .bar {
  background-image: linear-gradient(to right, #F19C42, #ffb544);
  height: 12px;
}
.progress.error > .bar {
  background-image: linear-gradient(to right, #B62F28, #E93F36);
  height: 12px;
}
.progress.error > .bar {
  background-image: linear-gradient(to right, #B62F28, #E93F36);
  height: 12px;
}
.progress.error > .bar {
  background-image: linear-gradient(to right, #B62F28, #E93F36);
  height: 12px;
}
.progress.indeterminate > .bar {
  position: absolute;
  background-image: linear-gradient(to right, #2C6776, #4EA35B);
  width: 0%;
  height: 12px;
  left: 0%;
  top: 3px;
  animation: indeterminate 1.200s ease infinite;
}

@keyframes indeterminate {
  0% {
    width: 30%;
    left: -.5%;
  }
  60% {
    left: 40%;
    width: 60%;
  }
  100% {
    left: 100%;
    width: 0%;
  }
}

