.radio {
  z-index: 5;
}
/* HIDE RADIO */
[type=radio] {
  position: absolute;
  opacity: 0;
  width: 0;
  height: 0;
}

/* IMAGE STYLES */
[type=radio] + .option {
  cursor: pointer;
}

/* CHECKED STYLES */
[type=radio]:checked + .option > .click {
  visibility: visible;
}
.option {
  text-transform: uppercase;
  font-family:"Pixel Cyr Normal";
  text-shadow: 4px 4px black;
  font-size: 20px;
  position: relative;
  padding: 1rem;
}
.click {
  background: url('data:image/png;base64,');
  background-repeat: no-repeat;
  background-size: 30px;
  width: 30px;
  height: 30px;
  position: absolute;
  transform: translate(-20px, 25px);
